@import url(https://fonts.googleapis.com/css?family=Nunito:400,700);
body,
html {
  height: 100%;
  margin: 0;
  font-family: 'Nunito', sans-serif;
}

div#root {
  display: contents;
  
}

body {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0e81e6+0,c706ff+100 */
  background: white; /* Old browsers */
 
}

aside {
  color: #fff;
  display: grid;
  grid-template-columns: auto 40px;
  padding: 2em;
  background: #b300ff;;
}

main ul {
  display: block;
}

img {
  width: 40px;
  cursor: pointer;
}

figure {
  margin: 0;
}

figcaption {
  font-size: 1.9em;
  font-weight: bold;
}

nav {
  background-color: #DCE1F6;
}

.active {
  position: absolute;
  display: block;
  background: #fff;
  width: 50%;
  left: 0;
  top: 0;
  height: 100%;
}

ul {
  display: none;
  list-style-type: none;
  margin: 0;
  padding: 0;

}

ul li a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: .8em;
  display: block;
  padding: 1.5em 3em;
  background-color: rgba(255,255,255,.35);
  color: #000;
}

main {
  background: #fff;
  padding: 1em 1.4em;
}


.links a{
  display: block;
  text-align: center;
  padding: 5px;
  font-size: 1.5em;
}

h1 {
  margin: 0;
}

p {
  font-size: 1.1em;
  line-height: 1.5em;
}

.cta {
  display: block;
  color: #fff;
  background: #b300ff;
  text-decoration: none;
  padding: 8px 50px;
  font-size: 1.2em;
  text-align: center;
  font-weight: bold;
  border-radius: 4px;
  margin: 2em 0;
}

.btn {
  font-size: 1.3em;
  margin: 0 1em;
  text-decoration: none;
  color: #0e7fff;
}

.btn:hover {
  color: #b300ff; 
}



/* about styles */

.services ul {
  list-style-type: square;
  padding-left: 50px;
}


/* portfolio styles */
.portfolio-item {

}

.portfolio-item img {
  width: 100%;
}


/* contact styles */
[type=color], 
[type=date], 
[type=datetime], 
[type=datetime-local], 
[type=email], 
[type=month], 
[type=number], 
[type=password],
[type=search], 
[type=tel], 
[type=text], 
[type=url], 
[type=week], 
[type=time], 
select, 
textarea {
  display: block;
  padding: .5rem;
  background: transparent;
  vertical-align: middle;
  width: 100%;
  max-width: 100%;
  border: 1px solid #cdcdcd;
  border-radius: 4px;
  font-size: .95rem;
  font-family: 'Nunito', sans-serif;
}

label {
  font-weight: 600;
  font-size: .9rem;
  display: block;
  margin: .5rem 0;
}

.submit-btn {
  display: block;
  color: #fff;
  background: #b300ff;
  text-decoration: none;
  padding: 8px 50px;
  font-size: 1.2em;
  text-align: center;
  font-weight: bold;
  border-radius: 4px;
  margin: 2em 0;
  border: 0;
}


@media only screen and (min-width: 768px) {

  body {
    display: grid;
    grid-template-columns: 20% auto;
    background: #DCE1F6;
  }

  aside img {
    display: none;
  }

  aside {
    background: #DCE1F6;
    grid-template-columns: auto;
    grid-template-rows: 20% auto;
    padding: 0;
  }

  main {
    padding: 4em;
    /* background: white url('images/background.svg') no-repeat bottom right;
    background-size: 78%; */
    overflow: scroll;
  }

  .links {
    display: inline;
  }

  .links a{
    display: inline;
    text-align: left;
    padding: 5px;
    font-size: 1.5em;
  }

  #avatar {
    border-radius: 50%;
    /*background: #fff;*/
    width: 50px;
    height: 50px;
    margin: 2em auto 1em auto;
  }

  figcaption {
    text-align: center;
    color: #000;
  }

  ul {
    display: block;
    margin-top: 2em;
    position: relative !important;
    background: none !important;
    width: 100% !important;
  }

  ul li a:hover {
    background-color: #fff;
  }

  nav {
    background: none;
  }

  .cta {
    width: 15%;
    display: inline-block;
    font-size: 1.3em;
    padding: 1em 2em;
  }

  

  h1 {
    font-size: 3em;
    margin-top: 2em;
  }

  p {
    font-size: 1.5em;
  }
}



